@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Poppins", sans-serif;
}
#root {
  height: 100vh;
}
@media only screen and (orientation: landscape) {
  #root {
    height: 100%;
  }
}
@media only screen and (min-width: 1440px) {
  html {
    font-size: 80%;
  }
}
@media only screen and (min-width: 1600px) {
  html {
    font-size: 95%;
  }
}
@media only screen and (min-width: 1800px) {
  html {
    font-size: 100%;
  }
}
@media only screen and (max-width: 1280px) {
  html {
    font-size: 60%;
  }
}
@media only screen and (max-width: 780px) {
  html {
    font-size: 58%;
  }
}
@media only screen and (max-width: 560px) {
  html {
    font-size: 55%;
  }
}

.App {
  width: 100vw;
  height: 100vh;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f2f2f2;
}
@media only screen and (max-width: 700px) {
  .App {
    width: 100%;
    height: 100%;
  }
}

@-webkit-keyframes loaderAnimation {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}

@keyframes loaderAnimation {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}

.loader-container {
  width: 39.7rem;
  padding: 3rem 2rem;
  background-color: white;
  text-align: left;
  box-shadow: 0px 2px 10px 0px #0000001a;
  border-radius: 1rem;
}
.status {
  margin-bottom: 2rem;
  font-weight: 500;
}
.loader {
  width: 100%;
  height: 0.5rem;
  border: 1rem;
  background-color: #f2f2f2;
  overflow: hidden;
  position: relative;
}
.loader-line {
  width: 8rem;
  height: 0.5rem;
  background-color: #2f80ed;
  border-radius: 1rem;
  position: relative;
  left: -100%;
  -webkit-animation: loaderAnimation 1.5s ease-in-out infinite;
          animation: loaderAnimation 1.5s ease-in-out infinite;
}

.btn {
  color: white;
  padding: 1rem 2rem;
  background-color: #2f80ed;
  border: none;
  border-radius: 1rem;
  cursor: pointer;
  transition: all 0.3s;
}
.btn:hover {
  background-color: #64a7ff;
}

.drop-zone {
  padding: 3rem 8rem;
  background-color: #f6f8fb;
  margin-top: 2.5rem;
  border-radius: 1rem;
  border: 1px dashed #97bef4;
  cursor: context-menu;
}
.icon {
  margin-bottom: 2rem;
}
.label {
  font-size: 1.2rem;
  color: #bdbdbd;
}
.img-zone {
  width: 38rem;
  height: 20rem;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.uploaded-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 1rem;
}
@media only screen and (max-width: 460px) {
  .img-zone {
    width: 100%;
  }
  .drop-zone {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 3rem 0;
  }
  .label {
    width: 80%;
  }
}

.uploader {
  background-color: white;
  box-shadow: 0px 2px 10px 0px #0000001a;
  padding: 4rem 3rem;
  border-radius: 1rem;
}
.uploader-title {
  font-weight: 500;
  margin-bottom: 1.5rem;
}
.file-type {
  color: #828282;
}
.separator {
  font-size: 1.2rem;
  color: #bdbdbd;
  margin: 2rem 0;
}
.file-input {
  display: none;
}
.tick-container {
  color: white;
  background-color: #219653;
  width: 3rem;
  margin: auto;
  font-size: 2rem;
  border-radius: 50%;
}
.uploaded-status {
  font-weight: 500;
  margin: 2rem 0;
  font-size: 2rem;
}
.img-source-container {
  display: flex;
  align-items: center;
  position: relative;
  width: 38rem;
  height: 3.5rem;
  background-color: #f6f8fb;
  border-radius: 1rem;
  padding-left: 1rem;
  padding-right: 11rem;
  border: 1px solid #e0e0e0;
}
.img-source-link {
  width: 100%;
  text-align: left;
  border: none;
  background-color: unset;
  overflow-y: hidden;
  overflow-x: scroll;
}
.img-source-link:focus {
  outline: none;
}
.img-source-link::-webkit-scrollbar {
  display: none;
}
.btn-link {
  position: absolute;
  right: 1px;
  padding: 0.8rem 2rem;
}
@media only screen and (max-width: 460px) {
  .uploader {
    width: 90%;
  }
  .img-source-container {
    width: 100%;
  }
}
@media only screen and (max-width: 280px) {
  .uploader {
    width: 100%;
  }
}

